(function () {
  var dataNS = 'mduiElementDataStorage';

  $.extend({
    /**
     * 
     * @param ele  DOM 
     * @param key 
     * @param value 
     */
    data: function (ele, key, value) {
      var data = {};

      if (value !== undefined) {
        //  keyvalue 
        data[key] = value;
      } else if (isObjectLike(key)) {
        // 
        data = key;
      } else if (key === undefined) {
        // 
        var result = {};
        each(ele.attributes, function (i, attribute) {
          var name = attribute.name;
          if (name.indexOf('data-') === 0) {
            var prop = name.slice(5).replace(/-./g, function (u) {
              // 
              return u.charAt(1).toUpperCase();
            });

            result[prop] = attribute.value;
          }
        });

        if (ele[dataNS]) {
          each(ele[dataNS], function (k, v) {
            result[k] = v;
          });
        }

        return result;
      } else {
        // 
        if (ele[dataNS] && (key in ele[dataNS])) {
          return ele[dataNS][key];
        } else {
          var dataKey = ele.getAttribute('data-' + key);
          if (dataKey) {
            return dataKey;
          } else {
            return undefined;
          }
        }
      }

      // 
      if (!ele[dataNS]) {
        ele[dataNS] = {};
      }

      each(data, function (k, v) {
        ele[dataNS][k] = v;
      });
    },

    /**
     * 
     * @param ele DOM 
     * @param key 
     */
    removeData: function (ele, key) {
      if (ele[dataNS] && ele[dataNS][key]) {
        ele[dataNS][key] = null;
        delete ele.mduiElementDataStorage[key];
      }
    },

  });

  $.fn.extend({

    /**
     * 
     * @param key 
     * @param value
     * @returns {*}
     */
    data: function (key, value) {
      if (value === undefined) {
        if (isObjectLike(key)) {

          // 
          return this.each(function (i, ele) {
            $.data(ele, key);
          });
        } else if (this[0]) {

          // 
          return $.data(this[0], key);
        } else {
          return undefined;
        }
      } else {
        // 
        return this.each(function (i, ele) {
          $.data(ele, key, value);
        });
      }
    },

    /**
     * 
     * @param key 
     * @returns {*}
     */
    removeData: function (key) {
      return this.each(function (i, ele) {
        $.removeData(ele, key);
      });
    },

  });
})();
